* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  width: 100vw;
  height: 100vh;
  background: #ccc;
}

body {
  width: 100vw;
  height: 100vh;
  font-family: Rubik;
  display: flex;
}

h1 {
  color: #4e6ec3;
  margin: 0;
  font-style: italic;
  font-weight: 900;
}

h2, h3 {
  font-style: italic;
  font-weight: 900;
  line-height: .85;
}

h3 {
  margin-bottom: .4rem;
  line-height: .85;
}

.sidebar {
  color: #fff;
  color: #888;
  min-width: 320px;
  background: #111;
  border-radius: 7px;
  flex-direction: column;
  margin: 20px;
  padding: 20px;
  display: flex;
}

.tagline-container {
  flex-direction: column;
  margin: 20px 0;
  display: flex;
}

.tagline {
  width: 70%;
  margin-bottom: 50px;
  font-size: 2rem;
  font-weight: 400;
}

.tagline-right {
  text-align: right;
  margin-left: auto;
}

.site-main {
  color: #888;
  height: calc(100vh - 40px);
  background: #fff;
  border-radius: 7px;
  flex-grow: 1;
  margin: 20px 20px 20px 0;
  padding: 20px;
  overflow: scroll;
}

.main-nav {
  border-top: 1px solid #fff3;
  flex-direction: column;
  margin-top: 1rem;
  display: flex;
}

.main-nav a {
  border-bottom: 1px solid #fff3;
  justify-content: space-between;
  margin: 0 0 .5rem;
  padding: .5rem .1rem;
  font-size: 2rem;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.main-nav a.active {
  color: #4e6ec3;
}

.socials {
  justify-content: space-between;
  margin-top: auto;
  display: flex;
}

@media screen and (min-width: 2400px) {
  .card-container {
    flex-flow: wrap;
    margin: -10px;
    display: flex;
  }

  .card-container .project-card {
    width: calc(50% - 20px);
    margin: 10px;
  }
}

.project-card {
  color: #000;
  aspect-ratio: 6 / 2;
  width: 100%;
  border-radius: 7px;
  justify-content: space-between;
  margin-bottom: 15px;
  text-decoration: none;
  display: flex;
  position: relative;
  overflow: hidden;
}

.project-card:last-of-type {
  margin-bottom: 0;
}

.project-card:hover {
  color: #000;
}

.project-card:hover .content {
  opacity: 1;
}

.project-card:hover .button {
  background: #000a;
}

.project-card:hover img {
  transform: scale(1.05)rotate(.25deg);
}

.project-card .content {
  color: #fff;
  z-index: 2;
  opacity: .7;
  mix-blend-mode: hard-light;
  background: linear-gradient(to right, #000d, #0000);
  flex-direction: column;
  justify-content: space-between;
  padding: 2rem 6rem 2rem 2rem;
  transition: all .3s;
  display: flex;
  position: relative;
}

.project-card .button {
  color: #fff;
  z-index: 2;
  align-item: space-around;
  background: none;
  border-left: 1px solid #ffffff80;
  flex-direction: column;
  justify-content: center;
  padding: 1.5rem;
  transition: all .3s;
  display: flex;
}

.project-card .title {
  font-size: 2rem;
}

.project-card .subtitle {
  font-weight: 500;
}

.project-card img {
  width: 100%;
  height: 100%;
  z-index: 0;
  object-fit: cover;
  transition: transform .3s;
  position: absolute;
  inset: 0;
  transform: scale(1)rotate(0);
}

.hero-cover {
  width: 100%;
  height: auto;
  border-radius: 10px;
  overflow: hidden;
}

.table-container {
  flex-direction: row;
  margin: -10px;
  display: flex;
}

.table-container > div {
  width: calc(50% - 20px);
  margin: 10px;
}

.table {
  flex-flow: column;
  margin: 15px 0;
  display: flex;
}

.table .tr {
  border-bottom: 1px solid #eee;
  padding: 15px 0;
  display: flex;
}

.table .tr .th {
  width: 10%;
  min-width: 140px;
  font-variant-numeric: tabular-nums;
  color: #999;
  font-weight: 500;
}

.table .tr .td {
  color: #222;
}

a {
  color: #ccc;
}

a:hover {
  color: #fff;
}

::selection {
  background-color: #fff5;
}

.menuToggle {
  color: #fff;
  width: 60px;
  height: 60px;
  z-index: 999;
  background: #4e6ec3;
  border: none;
  border-radius: 50%;
  font-size: 2rem;
  display: none;
  position: fixed;
  top: 30px;
  left: 30px;
  box-shadow: 0 0 10px #3338;
}

.menuToggle:hover {
  background: #2e4ea3;
}

@media screen and (max-width: 1000px) {
  .sidebar {
    height: calc(100% - 40px);
    min-width: 280px;
    max-width: 300px;
    z-index: 500;
    padding-right: 60px;
    transition: all .3s;
    position: fixed;
    top: 0;
    right: -50px;
    transform: translateX(110%);
  }

  .site-main {
    margin: 20px;
    transition: filter .3s;
  }

  .menuToggle {
    cursor: pointer;
    display: block;
  }

  .menuToggle .close {
    display: none;
  }

  .menuOpen .sidebar {
    transform: translateX(0);
  }

  .menuOpen .menuToggle .open {
    display: none;
  }

  .menuOpen .menuToggle .close {
    display: block;
  }

  .menuOpen .site-main {
    filter: blur(3px);
    overflow: hidden;
  }

  .tagline {
    width: 100%;
    margin-bottom: 10px;
    font-size: 1.5rem;
  }

  .tagline-right {
    text-align: left;
  }

  .table-container {
    flex-direction: column;
    margin: 0;
  }

  .table-container > div {
    width: 100%;
    margin: 0;
  }

  .project-card {
    aspect-ratio: 1 / 1;
  }

  .project-card .button {
    display: none;
  }

  .project-card .content {
    opacity: 1;
  }
}

